//import react into the bundle
import React from "react";
import ReactDOM from "react-dom";

// include your styles into the webpack bundle
import "../styles/index.css";

//font awesome
import { library } from "@fortawesome/fontawesome-svg-core";
import { faClock } from "@fortawesome/free-regular-svg-icons";

library.add(faClock);

//import your own components
import Home from "./component/home.jsx";

//render your react application
let i = 0;
function intervalRender() {

    i++;
    let numArr = String(i).padStart(6, "0").split('');
    let a = numArr[5];
    let b = numArr[4];
    let c = numArr[3];
    let d = numArr[2];
    let e = numArr[1];
    let f = numArr[0];

    ReactDOM.render(<Home a={a} b={b} c={c} d={d} e={e} f={f}/>, document.querySelector("#app"));
    if (i === 999999) clearInterval(intervalID);
}

let intervalID = setInterval(intervalRender, 1000);